.a{
    height:100vh;
    display: flex;
    align-items: center;
}
.a-left{
    flex:1;
   display: flex;
   align-items: center;
   justify-content: center;
   position: relative;
    height: 100%;
}

.a-right{
    flex:1
}

.a-card{
    width: 60%;
    height: 70vh;
    border-radius: 30px;
    position: relative;
    overflow: hidden;
}

.a-card.bg{
    position: absolute;
    top: 50px;
    left: 50px;
    background-color: #333;
}

.a-img {
    width: 100%;
    height:100%;
    object-fit: cover;
}



.a-award-img {
    margin: 5px;
 height: 120px;
 width: auto;

 
}

.a-award-img:hover{
    animation: spin 3s ease-in-out infinite alternate;
}

@keyframes spin {
    from { 
        transform: scale(300%);
        transform: rotate(0deg); 
    } to { 
        transform: scale(300%);
        transform: rotate(360deg); 
    }
}